.borders{
    border: 2px solid black;
    padding: 20px;

    color: black;
    font-weight: bold;

    display: grid;

    grid-template-areas:
    "photo genres"
    "title title";

}

.photo{
    grid-area: photo;
}
.genres{
    grid-area: genres;

}

.title{
    grid-area: title;
}
